.donate-hero-container{
    background: linear-gradient(0deg, rgba($dark-blue, 0.3), rgba($dark-blue, 0.3)), url('../../../assets/images/backgroundCard.jpg');
    background-position: 0px -150px;
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 500px;
    overflow: hidden;
    color: $dark-blue;

    .donate-hero-card{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        display: flex;
        flex-direction: column;
        align-items: center;
        width: max-content;
        padding: 1.5rem 3rem;
        background-color: $white;
        gap: 2rem;
        border-radius: 0.75rem;
        box-shadow: $shadow-2;
        .donate-hero-content{
            display: flex;
            align-items: center;
            flex-direction: column;
            gap: 1rem;
            .donate-title{
                text-align: center;
            }
            hr{
                opacity: 0.3;
            }
            .donate-hero-text{
                gap: 1rem;
                display: flex;
                flex-wrap: wrap;
                p{
                    opacity: 1;
                }
            }
        }
    }
}

@media screen and (max-width: 768px){
    .donate-hero-container{
        .donate-hero-card{
            width: 80%;
            .donate-hero-content{
                hr{
                    display: none;
                }
                .donate-hero-text{
                    display: flex;
                    justify-content: center;
                    text-align: center;
                }
            }
        }
    }
}

@media screen and (max-width: 480px){
    .donate-hero-container{
        .donate-hero-card{
            width: 80%;
            .donate-hero-content{
                .donate-hero-text{
                    gap: 0.5rem;
                }
                hr{
                    display: none;
                }
            }
        }
    }
}